import React, { useState, useRef, useEffect } from "react"; // Add useEffect to the import
import OutlineMicMicOn2 from "../../screens/Scenario/img/mic-on.svg";
import Rectangle44 from "../../screens/Scenario/img/rectangle-44.svg";
import StopIcon from "../../screens/Scenario/img/stop.svg";
import SpeakerIcon from "../../screens/Scenario/img/speaker.svg";
import { QuickTip } from "../QuickTip";
import "./style.css";

export const ComponentWrapper = ({ className, videoText, chineseAudio, currentVideoIndex }) => {
  const [showMicIcon, setShowMicIcon] = useState(true);
  const audioRef = useRef(null);

  const toggleIcon = () => {
    setShowMicIcon(prevState => !prevState);
  };

  useEffect(() => {
    // Reset the showMicIcon state whenever currentVideoIndex changes
    setShowMicIcon(true);}, [currentVideoIndex]);

  const renderVideoText = () => {
    if (typeof videoText !== 'string') return null; // Ensure videoText is a string
  
    const [chinese, english] = videoText.split('<br />');
  
    // Determine the class name based on the probability
    const getColorClass = () => {
      return Math.random() <= 0.8 ? "greenColor" : "redColor";
    };

    const renderChineseText = () => {
      const letters = chinese.split('');
      return letters.map((letter, index) => (
        <span
          key={index}
          className={showMicIcon ? '' : getColorClass()} // Apply the class only when showMicIcon is false
          style={{
            transitionDelay: showMicIcon ? '0s' : `${index * 0.3}s`, // Apply the delay only when showMicIcon is false
            display: 'inline-block',
          }}
        >
          {letter}
        </span>
      ));
    };
    
    
  
    const letters = chinese.split('');
  
    return (
      <>
        <span className="englishText">{english}</span>
        <br />
        {renderChineseText()} 
      </>
    );
  };

  const Placeholdertext = () => {
    return (
      <>
        <span className="promptText">Form the sentence below in Chinese</span>
      </>
    );
  }
  
  
  return (
    <div className={`component-wrapper ${className}`}>
      <div className="overlap-group-2">
        <QuickTip
          arrowClassName="quick-tip-6"
          arrowPosition="bottom-right"
          buttons={false}
          className="quick-tip-5"
          color="white"
          divClassName="quick-tip-7"
          divClassNameOverride="quick-tip-8"
          mainClassName="quick-tip-9"
          text="You:"
          text1= {Placeholdertext()}
          text2 = {renderVideoText()}
        />
        {/* <img className="speaker" alt="speakericon" src={SpeakerIcon} onClick={playAudio} />
        <audio ref={audioRef} src={chineseAudio}></audio> */}
        <div onClick={toggleIcon}>
          <img className="rectangle" alt="Rectangle" src={Rectangle44} />
          <img className="outline-mic-mic-on" alt="Mic Icon" src={showMicIcon ? OutlineMicMicOn2 : StopIcon} />
        </div>
      </div>
    </div>
  );
};