import PropTypes from "prop-types";
import React from "react";
import "./style.css";
import Arrow57 from "../../screens/Scenario/img/arrow-57.svg";
import Arrow56 from "../../screens/Scenario/img/arrow-56.svg";
import Arrow55 from "../../screens/Scenario/img/arrow-55.svg";
import Arrow2 from "../../screens/Scenario/img/arrow-2.svg";
import Arrow48 from "../../screens/Scenario/img/arrow-48.svg";
import Arrow47 from "../../screens/Scenario/img/arrow-47.svg";
import Arrow46 from "../../screens/Scenario/img/arrow-46.svg";
import Arrow45 from "../../screens/Scenario/img/arrow-45.svg";
import Arrow44 from "../../screens/Scenario/img/arrow-44.svg";
import Arrow18 from "../../screens/Scenario/img/arrow-18.svg";
import Arrow17 from "../../screens/Scenario/img/arrow-17.svg";
import Arrow16 from "../../screens/Scenario/img/arrow-16.svg";
import Arrow15 from "../../screens/Scenario/img/arrow-15.svg";
import Arrow14 from "../../screens/Scenario/img/arrow-14.svg";
import Arrow8 from "../../screens/Scenario/img/arrow-8.svg";
import Arrow7 from "../../screens/Scenario/img/arrow-7.svg";
import Arrow6 from "../../screens/Scenario/img/arrow-6.svg";
import Arrow5 from "../../screens/Scenario/img/arrow-5.svg";
import Arrow4 from "../../screens/Scenario/img/arrow-4.svg";
import Arrow78 from "../../screens/Scenario/img/arrow-78.svg";
import Arrow77 from "../../screens/Scenario/img/arrow-77.svg";
import Arrow76 from "../../screens/Scenario/img/arrow-76.svg";
import Arrow75 from "../../screens/Scenario/img/arrow-75.svg";
import Arrow74 from "../../screens/Scenario/img/arrow-74.svg";
import Arrow68 from "../../screens/Scenario/img/arrow-68.svg";
import Arrow67 from "../../screens/Scenario/img/arrow-67.svg";
import Arrow66 from "../../screens/Scenario/img/arrow-66.svg";
import Arrow65 from "../../screens/Scenario/img/arrow-65.svg";
import Arrow64 from "../../screens/Scenario/img/arrow-64.svg";
import Arrow38 from "../../screens/Scenario/img/arrow-38.svg";
import Arrow37 from "../../screens/Scenario/img/arrow-37.svg";
import Arrow36 from "../../screens/Scenario/img/arrow-36.svg";
import Arrow35 from "../../screens/Scenario/img/arrow-35.svg";
import Arrow34 from "../../screens/Scenario/img/arrow-34.svg";
import Arrow28 from "../../screens/Scenario/img/arrow-28.svg";
import Arrow27 from "../../screens/Scenario/img/arrow-27.svg";
import Arrow26 from "../../screens/Scenario/img/arrow-26.svg";
import Arrow25 from "../../screens/Scenario/img/arrow-25.svg";
import Arrow24 from "../../screens/Scenario/img/arrow-24.svg";
import Arrow58 from "../../screens/Scenario/img/arrow-58.svg";


export const QuickTip = ({
  color,
  arrowPosition,
  buttons,
  className,
  arrowClassName,
  divClassName,
  text = "New feature!",
  divClassNameOverride,
  text1 = "",
  text2 = "",
  mainClassName,
}) => {
  return (
    <div className={`quick-tip ${arrowPosition} ${color} buttons-${buttons} ${className}`}>
      {["bottom-left", "bottom-right", "right-bottom", "right-top"].includes(arrowPosition) && (
        <div className={`main ${arrowClassName}`}>
          <div className="text">
            <div className={`new-feature ${!buttons ? divClassName : undefined}`}>{text}</div>
            <p className={`this-is-a-handyy`}>{text1}</p>
            <p className={`this-is-a-handy ${!buttons ? divClassNameOverride : undefined}`}>{text2}</p>
          </div>
          {buttons && (
            <div className="frame">
              <button className="button">
                <div className="text-wrapper">Prev</div>
              </button>
              <button className="div">
                <div className="text-wrapper-2">Next</div>
              </button>
            </div>
          )}
        </div>
      )}

      {(arrowPosition === "bottom-left" ||
        arrowPosition === "bottom-right" ||
        arrowPosition === "left-bottom" ||
        arrowPosition === "left-top" ||
        arrowPosition === "right-bottom" ||
        arrowPosition === "right-top" ||
        arrowPosition === "top-left" ||
        arrowPosition === "top-right") && (
        <img
          className={`arrow ${
            ["bottom-left", "bottom-right", "right-bottom", "right-top"].includes(arrowPosition)
              ? mainClassName
              : ["left-bottom", "left-top", "top-left", "top-right"].includes(arrowPosition)
              ? arrowClassName
              : undefined
          }`}
          alt="Arrow"
          src={
            color === "green" && arrowPosition === "bottom-left"
              ? Arrow57
              : color === "orange" && arrowPosition === "bottom-left"
              ? Arrow56
              : color === "dark" && arrowPosition === "bottom-left"
              ? Arrow55
              : arrowPosition === "bottom-left" && color === "white"
              ? Arrow2
              : color === "blue" && arrowPosition === "bottom-right"
              ? Arrow48
              : color === "green" && arrowPosition === "bottom-right"
              ? Arrow47
              : color === "orange" && arrowPosition === "bottom-right"
              ? Arrow46
              : color === "dark" && arrowPosition === "bottom-right"
              ? Arrow45
              : color === "white" && arrowPosition === "bottom-right"
              ? Arrow44
              : color === "blue" && arrowPosition === "right-top"
              ? Arrow18
              : arrowPosition === "right-top" && color === "green"
              ? "/img/arrow-17.svg"
              : arrowPosition === "right-top" && color === "orange"
              ? Arrow16
              : color === "dark" && arrowPosition === "right-top"
              ? Arrow15
              : arrowPosition === "right-top" && color === "white"
              ? Arrow14
              : color === "blue" && arrowPosition === "right-bottom"
              ? Arrow8
              : color === "green" && arrowPosition === "right-bottom"
              ? Arrow7
              : color === "orange" && arrowPosition === "right-bottom"
              ? Arrow6
              : color === "dark" && arrowPosition === "right-bottom"
              ? Arrow5
              : color === "white" && arrowPosition === "right-bottom"
              ? Arrow4
              : color === "blue" && arrowPosition === "left-top"
              ? Arrow78
              : arrowPosition === "left-top" && color === "green"
              ? Arrow77
              : arrowPosition === "left-top" && color === "orange"
              ? Arrow76
              : color === "dark" && arrowPosition === "left-top"
              ? Arrow75
              : arrowPosition === "left-top" && color === "white"
              ? Arrow74
              : color === "blue" && arrowPosition === "top-left"
              ? "/img/arrow-68.svg"
              : color === "green" && arrowPosition === "top-left"
              ? "/img/arrow-67.svg"
              : color === "orange" && arrowPosition === "top-left"
              ? "/img/arrow-66.svg"
              : color === "dark" && arrowPosition === "top-left"
              ? "/img/arrow-65.svg"
              : color === "white" && arrowPosition === "top-left"
              ? Arrow64
              : color === "blue" && arrowPosition === "top-right"
              ? "/img/arrow-38.svg"
              : arrowPosition === "top-right" && color === "green"
              ? "/img/arrow-37.svg"
              : arrowPosition === "top-right" && color === "orange"
              ? "/img/arrow-36.svg"
              : color === "dark" && arrowPosition === "top-right"
              ? "/img/arrow-35.svg"
              : arrowPosition === "top-right" && color === "white"
              ? Arrow34
              : color === "blue" && arrowPosition === "left-bottom"
              ? "/img/arrow-28.svg"
              : arrowPosition === "left-bottom" && color === "green"
              ? "/img/arrow-27.svg"
              : arrowPosition === "left-bottom" && color === "orange"
              ? "/img/arrow-26.svg"
              : color === "dark" && arrowPosition === "left-bottom"
              ? "/img/arrow-25.svg"
              : arrowPosition === "left-bottom" && color === "white"
              ? Arrow24
              : Arrow58
          }
        />
      )}

      {arrowPosition === "no-arrow" && (
        <div className="main-2">
          <div className="text">
            <div className="new-feature-2">{text}</div>
            <p className="p">{text1}</p>
          </div>
          {buttons && (
            <div className="frame">
              <button className="button-2">
                <div className="text-wrapper-3">Prev</div>
              </button>
              <button className="button-3">
                <div className="text-wrapper-4">Next</div>
              </button>
            </div>
          )}
        </div>
      )}

      {["left-bottom", "left-top", "top-left", "top-right"].includes(arrowPosition) && (
        <div className={`main-3 ${mainClassName}`}>
          <div className="text">
            <div className="new-feature-3">{text}</div>
            <p className="this-is-a-handy-2">{text1}</p>
          </div>
          {buttons && (
            <div className="frame">
              <button className="button-4">
                <div className="text-wrapper-5">Prev</div>
              </button>
              <button className="button-5">
                <div className="text-wrapper-6">Next</div>
              </button>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

QuickTip.propTypes = {
  color: PropTypes.oneOf(["white", "blue", "orange", "green", "dark"]),
  arrowPosition: PropTypes.oneOf([
    "top-left",
    "bottom-left",
    "right-top",
    "bottom-right",
    "left-top",
    "left-bottom",
    "right-bottom",
    "no-arrow",
    "top-right",
  ]),
  buttons: PropTypes.bool,
  text: PropTypes.string,
  text1: PropTypes.string,
};
